.item {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 300px;
    height: 70px;
    border: 1px solid rgb(125, 125, 125);
    padding: 5px 15px;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.status {
    width: 15px;
    height: 15px;
    border-radius: 50%;
}

.online {
    background-color: #04a504;
}

.offline {
    background-color: #dc143c;
}