.transactionHistory{
    width: 600px;
    text-align: center;
    font-size: 16px;
    line-height: 2;
    margin: 20px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    
}

table,
th,
td {
    border: 1px solid rgb(255, 255, 255);
    border-collapse: collapse;
}

th{
    background-color: rgb(0, 242, 255);
    color: #ffffff;
}

tr:nth-child(even){
    background-color: #e0dede;
}